import { useEffect } from "react";
import { AnyFunction, __GLOBAL__ } from "@m78/utils";
import { useFn, useSelf } from "../../";

export interface UseThrottleOption {
  /** true |  */
  leading?: boolean;
  /** true |  */
  trailing?: boolean;
}

const defaultOption = {
  leading: true,
  trailing: true,
};

/**
 * , 
 * @param fn - 
 * @param wait - 
 * @param options
 * @param options.leading - true | 
 * @param options.trailing - true | 
 * @returns throttleFn - 
 * @returns throttleFn.cancel() - 
 */
export function useThrottle<T extends AnyFunction>(
  fn: T,
  wait = 300,
  options?: UseThrottleOption
) {
  const self = useSelf({
    last: 0,
    timer: undefined as any,
  });

  const opt = {
    ...defaultOption,
    ...options,
  };

  const cancel = useFn(() => {
    if (self.timer) {
      __GLOBAL__.clearTimeout(self.timer);
    }
  });

  useEffect(() => {
    return cancel;
  });

  const memoFn = useFn((...args) => {
    const now = Date.now();
    const diff = now - self.last;

    cancel();

    if (diff > wait) {
      // last = 0 
      if (opt.leading || self.last !== 0) {
        fn(...args);
      }
      self.last = now;
    } else if (opt.trailing) {
      self.timer = __GLOBAL__.setTimeout(() => {
        fn(...args);
        self.last = 0; // leading
        __GLOBAL__.clearTimeout(self.timer);
      }, wait);
    }
  });

  const bundle = Object.assign(memoFn as T, {
    cancel,
  });

  return bundle;
}
