import { __GLOBAL__, isEmpty, isFunction, isObject } from "@m78/utils";
import _debounce from "lodash/debounce";
import _throttle from "lodash/throttle";
import React, { useEffect, useState } from "react";
import {
  useEffectEqual,
  useFn,
  useSelf,
  useSetState,
  useStorageState,
  SetStateBase,
} from "../../";

const GLOBAL = __GLOBAL__ as Window;

/* TODO:  */

interface UseFetchOptions<Data, Payload> {
  /** [] | useEffect(fn, deps), memo */
  deps?: any[];
  /** , PayloadpayloadPayload */
  param?: Payload;
  /** false | send */
  manual?: boolean;
  /** 10000 |  ms */
  timeout?: number;
  /** true | true,  */
  pass?: boolean;
  /** data */
  initData?: (() => Data) | Data;
  /** payload, param */
  initPayload?: (() => Payload) | Payload;
  /** , (senddeps)isUpdatetrue */
  onSuccess?: (result: Data, isUpdate: boolean) => void;
  /**  error */
  onError?: (error: any) => void;
  /**  */
  onComplete?: () => void;
  /**  */
  onTimeout?: () => void;
  /** key(payload, data, arg)session */
  cacheKey?: string;
  /** true | cacheKeyswr(stale-while-revalidate) */
  stale?: boolean;
  /** ,  */
  throttleInterval?: number;
  /** , , throttleInterval */
  debounceInterval?: number;
  /** , 500 */
  pollingInterval?: number;
}

interface UseFetchReturns<Data, Payload> {
  /**  */
  loading: boolean;
  /** methodrejecterrorreject */
  error: any;
  /** true */
  timeout: boolean;
  /** methodresolveinitData */
  data: Data;
  /** payloadinitPayload */
  payload: Payload;
  /** param */
  param: Payload;
  /** data */
  setData: SetStateBase<Data>;
  /**  */
  cancel: () => void;
  /** pollingInterval */
  polling: boolean;
  /**  */
  setPolling(patch: boolean | ((prev: boolean) => boolean)): void;
  /**
   * :
   * - : payloadpayload
   * - /: 
   * - param: paramparam. payload
   *
   * Promise:
   * - resolve[err, res]Promiseerrreject(null)resresolve
   * - pass
   * */
  send: (
    newPayload?:
      | Payload
      | React.SyntheticEvent
      | undefined /* SyntheticEventsendonClick */
  ) => Promise<[any, Data]>;
}

/**  */
function isSyntheticEvent(arg: any) {
  if (!arg) return false;

  return (
    isObject(arg) && "nativeEvent" in arg && "target" in arg && "type" in arg
  );
}

function useFetch<Data = any, Payload = any>(
  /** Promise returnasync,  */
  method?: ((...arg: any[]) => Promise<Data>) | any,
  /**  */
  options = {} as UseFetchOptions<Data, Payload>
) {
  const self = useSelf({
    /**  */
    fetchID: 0,
    /**  */
    fetchCount: 0,
    /**  */
    timeoutTimer: 0,
    /**  */
    lastFetch: Date.now(),
  });
  const {
    initData,
    initPayload,
    deps = [],
    param,
    manual = false,
    timeout = 10000,
    onSuccess,
    onError,
    onComplete,
    onTimeout,
    cacheKey,
    stale = true,
    throttleInterval,
    debounceInterval,
    pollingInterval,
    pass: aPass = true,
  } = options;

  const isCache = !!cacheKey; // keyisPost

  const pass = aPass && isFunction(method);

  const [state, setState] = useSetState({
    loading: !manual && pass,
    error: undefined as any,
    timeout: false,
  });

  // 
  useEffect(() => {
    return () => {
      self.fetchID = 0; // 
      self.timeoutTimer && GLOBAL.clearTimeout(self.timeoutTimer);
    };
  }, []);

  const [payload, setPayload] = useStorageState(
    `${cacheKey}_FETCH_PAYLOAD`,
    initPayload,
    {
      disabled: !isCache,
    }
  );

  const [data, setData] = useStorageState(`${cacheKey}_FETCH_DATA`, initData, {
    disabled: !isCache,
  });

  const [polling, setPolling] = useState(true);

  const fetchHandel = useFn(
    async function _fetchHandel(args: any, isUpdate = false) {
      if (!pass) {
        return [new Error("the request has been ignored"), null];
      }

      self.lastFetch = Date.now();

      const cID = Math.random();
      self.fetchID = cID;

      // 
      self.timeoutTimer && GLOBAL.clearTimeout(self.timeoutTimer);
      self.timeoutTimer = GLOBAL.setTimeout(() => {
        cancel();
        onTimeout?.();
        setState({
          ...getResetState("timeout", true),
        });
      }, timeout);

      // 
      const cTimeoutTimer = self.timeoutTimer;

      // 
      if (!state.loading) {
        setState({
          ...getResetState("loading", true),
        });
      }

      try {
        const res = await method(args);
        if (cID === self.fetchID) {
          setState({
            ...getResetState("loading", false),
          });
          setData(res);
          onSuccess?.(res, isUpdate);
          return [undefined, res];
        }
      } catch (err) {
        if (cID === self.fetchID) {
          setState({
            ...getResetState("error", err),
          });
          onError?.(err);
          return [err, undefined];
        }
      } finally {
        // 
        cTimeoutTimer && GLOBAL.clearTimeout(cTimeoutTimer);

        if (cID === self.fetchID) {
          onComplete?.();
          self.fetchCount++;
        }
      }

      // 
      return [new Error("never execute"), null];
    },
    (fn) => {
      if (throttleInterval) {
        return _throttle(fn, throttleInterval, { trailing: false }); // 
      }

      if (debounceInterval) {
        return _debounce(fn, debounceInterval);
      }

      return fn as any;
    }
  );

  /**  */
  const send = useFn((newPayload?: Payload) => {
    const isUpdate = isSyntheticEvent(newPayload) || newPayload === undefined;
    return fetchHandel(getActualPayload(newPayload), isUpdate);
  });

  /** param */
  useEffectEqual(() => {
    if (!("param" in options)) return;
    if (self.fetchCount === 0 || manual) return;
    fetchHandel(getActualPayload(), false); // 
  }, [param]);

  /**  */
  useEffect(() => {
    if (manual || !pass) return;
    // stale
    if (!stale && self.fetchCount === 0 && !isEmpty(data)) {
      setState({
        loading: false,
      });
      return;
    }
    fetchHandel(getActualPayload(), self.fetchCount !== 0);
  }, [pass, ...deps]);

  /**  */
  useEffect(
    function intervalHandle() {
      let timer: number;

      if (polling && pollingInterval && pollingInterval > 500) {
        timer = GLOBAL.setInterval(() => {
          const now = Date.now();
          const last = self.lastFetch;
          const reFetch = now - last >= pollingInterval;
          reFetch && send();
        }, pollingInterval);
      }

      return () => {
        timer && clearInterval(timer);
      };
    },
    [pollingInterval, polling]
  );

  /** payloadfetchHandel(parampayload) */
  function getActualPayload(newPayload?: Payload) {
    // paramparam
    if ("param" in options) {
      return param;
    }

    // payload
    if (isSyntheticEvent(newPayload) || newPayload === undefined) {
      return payload;
    }

    // payloadpayloadpayload
    setPayload(newPayload as Payload); // payload
    return newPayload;
  }

  /**  */
  function getResetState(key?: string, value?: any) {
    const resetState: any = {
      loading: false,
      error: undefined,
      timeout: false,
    };

    if (key) {
      resetState[key] = value;
    }

    return resetState;
  }

  /**  */
  function cancel() {
    self.fetchID = 0; // 
    self.timeoutTimer && GLOBAL.clearTimeout(self.timeoutTimer);
    setState({
      loading: false,
    });
  }

  return {
    ...state,
    send,
    data,
    payload,
    param,
    setData,
    cancel,
    polling,
    setPolling,
  } as any as UseFetchReturns<Data, Payload>;
}

export { useFetch };
